import { MetaSwiper, SafariSwiper } from "./meta-settings.js";

$(document).ready(function () {
  const header = document.getElementsByClassName(`header`)[0];
  // Header scroll
  const scrollContainer = () => {
    return document.documentElement || document.body;
  };

  if (header.classList.contains("fixed")) {
    document.addEventListener("scroll", () => {
      if (scrollContainer().scrollTop > 0) {
        header.classList.add("scrolled");
      } else if (scrollContainer().scrollTop == 0) {
        header.classList.remove("scrolled");
      }
    });
  }

  // menu handlers

  ///check mobile menu show/hide condition
  const mobileMenuStartPoint = +getComputedStyle(
    document.documentElement
  ).getPropertyValue("--mobile-menu-start-point");
  let isMobileMenuEnable =
    $(window).outerWidth() <= mobileMenuStartPoint ||
    $(".header-mobile").length;

  // toggle menu handler
  function menuToggle() {
    $(".menu-toggle").toggleClass("active");
    $(".navbar-nav").toggleClass("active");
    $(".header-close-wrapper").toggleClass("active");
    // LockScroll when burger open and enable when closed and enable scroll on menu
    scrollLock.getScrollState()
      ? scrollLock.disablePageScroll(
          document.querySelector(".navbar-nav .menu")
        )
      : scrollLock.enablePageScroll();
  }

  $(".menu-toggle").click(menuToggle); //menu toggles
  $(".header-close-wrapper").click(menuToggle); //menu toggles

  // Swiper slider + resize -------------------
  let mySwiper;
  const sliderBox = document.querySelector(".slider__box");
  const sliderWrapper = document.querySelector(".slider__wrapper");
  const sliderSlide = document.querySelectorAll(".slider__slide");

  function createSwiper() {
    mySwiper = new Swiper(".slider__box", {
      speed: 700,
      observer: true,
      spaceBetween: 5,
      initialSlide: 1,
      centeredSlides: true,
      autoplay: {
        delay: 5000,
        disableOnInteraction: false,
        pauseOnMouseEnter: true,
      },
      pagination: {
        el: ".swiper-pagination",
        type: "bullets",
        clickable: true,
      },
      navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev",
      },

      breakpoints: {
        320: {
          slidesPerView: 1,
          spaceBetween: 2,
        },
        500: {
          slidesPerView: 2,
          spaceBetween: 2,
        },
        769: {
          slidesPerView: 3,
          spaceBetween: 5,
        },
        901: {
          slidesPerView: 3,
          spaceBetween: 10,
        },
      },
    });
  }

  function destroySwiper() {
    if (mySwiper !== undefined) {
      sliderBox.classList.remove("swiper");
      mySwiper.destroy();
    }
  }

  function removeClasses() {
    if (window.innerWidth <= 901) {
      sliderBox.classList.add("swiper");
      sliderWrapper.classList.add("swiper-wrapper");
      sliderSlide.forEach((el) => {
        el.classList.add("swiper-slide");
      });
    } else {
      sliderBox.classList.remove("swiper");
      sliderWrapper.classList.remove("swiper-wrapper");
      sliderSlide.forEach((el) => {
        el.classList.remove("swiper-slide");
      });
    }
  }

  function checkWidth() {
    if (window.innerWidth <= 901) {
      removeClasses();
      createSwiper();
    } else {
      destroySwiper();
      removeClasses();
    }
  }

  checkWidth();
  window.onresize = checkWidth;
  // Swiper slider + resize -------------------

  // Quiz ---------------------------
  class Quiz {
    constructor(question, step, stepTotal, btnYes, btnNo) {
      this.question = question;
      this.step = step;
      this.stepTotal = stepTotal;
      this.btnYes = btnYes;
      this.btnNo = btnNo;
      this.questions = [
        "Avete mai usato un Set Bracciale Sogna in Grande?",
        "Vi piace il colore rosso?",
        "Preferite viaggiare in treno o in aereo?",
      ];
      this.currentQuestionIndex = 0;
    }

    start() {
      this.question.innerHTML = this.questions[this.currentQuestionIndex];
      this.step.innerHTML = "1";
      this.stepTotal.innerHTML = this.questions.length;

      this.btnYes.addEventListener("click", (e) => {
        this.nextStep();
      });

      this.btnNo.addEventListener("click", (e) => {
        this.nextStep();
      });
    }

    nextStep() {
      this.currentQuestionIndex++;
      if (this.currentQuestionIndex < this.questions.length) {
        this.question.innerHTML = this.questions[this.currentQuestionIndex];
        this.step.innerHTML = this.currentQuestionIndex + 1;
      } else {
        this.btnYes.setAttribute("href", "https://www.vnm45rtghost.com");
        this.btnNo.setAttribute("href", "https://www.vnm45rtghost.com");
      }
    }
  }

  const question = document.querySelector(".quiz__questions");
  const step = document.querySelector(".quiz__step");
  const stepTotal = document.querySelector(".quiz__steps-total");
  const btnYes = document.querySelector(".quiz__btn-yes");
  const btnNo = document.querySelector(".quiz__btn-no");
  const quiz = new Quiz(question, step, stepTotal, btnYes, btnNo);
  quiz.start();
});
